import React from 'react';
import PropTypes from 'prop-types';
import PageWrapper from 'components/templates/PageWrapper/PageWrapper';
import { StyledForm, LogoWrapper, StyledButton, StyledSpan } from './Login.style';
import Input from 'components/atoms/Input/Input';
import { ReactComponent as Logo } from 'assets/icons/logo.svg';

const Login = (props) => {
  return (
    <PageWrapper>
      <LogoWrapper>
        <Logo />
      </LogoWrapper>
      <StyledForm>
        <Input placeholder="Email" type="email" />
        <Input placeholder="Hasło" type="password" />
        <StyledButton>ZALOGUJ SIĘ</StyledButton>
      </StyledForm>
      <StyledSpan>Nie masz jeszcze konta?</StyledSpan>
      <StyledSpan color>Zarejestruj się!</StyledSpan>
    </PageWrapper>
  );
};

Login.propTypes = {};

export default Login;
